<template>
  <div id="app">
    <aia-head ref="aiaHead" :showLeft="showLeft" :showRight="showRight" :title="title"></aia-head>
    <router-view />
  </div>
</template>
<script>
import { mapState, mapGetters } from "vuex";

import AiaHead from "./components/AiaHead";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapState("moduleHead", {
      title: state => state.title,
      showLeft: state => state.showLeft,
      activeName: state => state.activeName,
      showRight: state => state.showRight
    })
  },
  components: {
    AiaHead
  },
  watch: {
    $route(to, from) {
      this.$store.commit("moduleHead/changeTitle", to.name);
      // this.$emit("hideMenueList");
      this.$refs.aiaHead.hideList();
    }
  }
};
</script>

