const state = {
    title: "iPLAN",
    showLeft: false,
    showRight: false,
    activeName: "CUSTOMER"

}
const mutations = {
    changeTitle(state, payload) {
        switch (payload) {
            case "customerinfo":
                state.title = "CUSTOMER INFO"
                state.activeName = "CUSTOMER"
                state.showLeft = true
                state.showRight = true
                break;
            case "segmentationOptions":
                state.title = "SEGMENTATION"
                state.activeName = "SEGMENTATION"
                state.showLeft = true
                state.showRight = true
                break;
            case "fhc":
            case "life_protection":
                state.title = "FHC"
                state.showLeft = true
                state.showRight = true
                state.activeName = "FHC"
                break;
            case "product":
                state.title = "PRODUCT RECOMMENDATION"
                state.activeName = "RECOMMENDATION"
                state.showLeft = true
                state.showRight = true
                break;
            case "consent":
                state.title = "PERSONAL DATA COLLECTION AND USE"
                state.showLeft = true
                break;
            case "ipos":
            case "homeSelect":
                state.showLeft = false
                state.showRight = false
                break;

            case "home":
                state.title = "iFHC"
                state.title = " GO TO iPoS+"
                state.showLeft = true
                break;
            default:
                state.title = "iPLAN"
                    // state.showLeft = false
                    // state.showRight = false
                break;
        }

    }
}
const actions = {
    changeTitle({
        state,
        rootState,
        commit,
        dispatch,
        getters,
        rootGetters
    }, payload) {
        commit("changeTitle", payload)

    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
}