const validateTool = (rule, val) => {
    // console.log(rule, val);
    let resault = true
    for (const item in rule) {

        let validator = rule[item].find((i) => {
            if (i.hasOwnProperty("validator")) {
                return i
            } else {
                return false
            }
        })
        let isRequired = rule[item].find((i) => {
            if (i.hasOwnProperty("required")) {
                return i
            } else {
                return false
            }
        })
        let isMin = rule[item].find((i) => {
            if (i.hasOwnProperty("min")) {
                return i
            } else {
                return false
            }
        })
        let isMax = rule[item].find((i) => {
            if (i.hasOwnProperty("max")) {
                return i
            } else {
                return false
            }
        })

        if (validator) {
            resault = validator.validator.test(val[item])
        }
        if (isRequired && isRequired.required && !val[item]) {
            resault = false
        }
        if (isMin && isMin.min && (val[item].length < isMin.min)) {
            resault = false
        }
        if (isMax && isMax.max && (val[item].length > isMax.max)) {
            resault = false
        }
        // return resault

    }

    return resault


}


export {
    validateTool
}